body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

body * {
  font-family: 'Roboto', sans-serif;
  outline: none;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 5fr;
}

h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span {
  border-bottom: 1px solid #C0C0C0;
  display: inline-block;
  padding-bottom: 5px;
}

a {
  text-decoration: none;
  color: #000;
}